const modal_button = document.querySelector('.burger_button')
const close_modal = document.querySelector('.button_close')
const modal = document.querySelector('.burger_menu_max')
modal_button.addEventListener('click', openModal)
close_modal.addEventListener('click', closeModal)
function openModal(e) {
  modal.style.display = 'block'
}
function closeModal(e) {
  modal.style.display = 'none'
}

